import styled from 'styled-components';
// import SliderImg1 from './../../assets/images/1.jpg';
// import SliderImg2 from './../../assets/images/2.jpg';
// import SliderImg3 from './../../assets/images/3.jpg';
// import SliderImg4 from './../../assets/images/4.jpg';

export default styled.div`
overflow-x: hidden;
.top-header{
    position: fixed;
    top: 0;
    right: 0;
    width: max-content;
    z-index: 1;
    .wrap-content{
        padding: 20px 0;
        background-color: ${props => props.theme.primary};
        p{
            padding: 0 15px;
            border-right: 1px solid #fff;
            color: #fff;
            font-size: 12px;
            font-style: italic;
            span.icon{
                position: relative;
                top: 3px;
                margin-right: 7px;
            }
        }
        &:last-child{
            p{
                border-right: none;
            }
        }
    }
    @media (max-width: 993px){
        display: none;
    }
}
.main-container{
    .wrap-slider{
        height: calc(100vh - 70px);
        width: calc(100% - 40px);
        margin: 30px auto 40px 0;
        position: relative;
        *:focus {
            outline: 0;
            outline: none;
        }
        .slick-slider, .slick-list, .slick-track, .slick-slide, .img-container{
            height: 100%;
        }
        .slick-slide > div{
            height: 100%;
        }
        .slick-slider{
            .img-container{
                background-size: cover;
                background-position: center;
                &.img-1{
                    background-image: url('https://www.dropbox.com/s/xxi0xe2uq6wpgsm/slide-1.jpg?dl=1');
                }
                &.img-2{
                    background-image: url('https://www.dropbox.com/s/4yffmxlc7f4rh6c/slide-2.jpg?dl=1');
                }
                &.img-3{
                    background-image: url('https://www.dropbox.com/s/aon8rprag1h7hd2/slide-3.jpg?dl=1');
                }
                &.img-4{
                    background-image: url('https://www.dropbox.com/s/zh3d1twbgst4zuh/slide-4.jpg?dl=1');
                }
            }
            .custom-dots{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 0;
                margin-right: 10px;
                li{
                    margin-bottom: 10px;
                    position: relative;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    button{
                        border: 3px solid ${props => props.theme.primary};
                        outline: none;
                        width: 15px;
                        height: 15px;
                        position: relative;
                        overflow: hidden;
                        &:after{
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            background-color: #fff;
                            cursor: pointer;
                        }
                    }
                    &.slick-active{
                        button::after{
                            background-color: ${props => props.theme.primary};
                        }
                    }
                }
            }
        }
        .wrap-carousel{
            height: 100%;
            position: relative;
            overflow: hidden;
            transform: translate3d(0, 0, 0);
            .slide-images{
                height: 100%;
                width: max-content;
                animation: moveSlideshow 40s linear infinite;
                .img-container{
                    height: 100%;
                    width: max-content;
                    transform: translate3d(0, 0, 0);
                    img{
                        height: 100%;
                        width: auto;
                    }
                }
            }

            @keyframes moveSlideshow {
                100% { 
                    transform: translateX(-50%);  
                }
            }
        }
        .wrap-event{
            display: none;
        }
        &.show-event{
            .wrap-event{
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                width: 400px;
                overflow: hidden;
                > .pos-relative{
                    padding-right: 10px;
                    > .pos-relative{
                        height: 100%;
                        border: 1px solid ${props => props.theme.border};
                        background-color: #dededea3;
                        img{
                            max-width: 100%;
                            max-height: 100%;
                        }
                    }
                    
                }
            }
            .carousel{
                width: calc(100% - 400px);
                margin-left: auto;
            }
            @media(max-width: 769px) {
                .wrap-event{
                    width: 300px;
                }
                .carousel{
                    width: calc(100% - 300px);
                }
            }
            @media(max-width: 520px) {
                .wrap-event{
                    width: 100%;
                    > .pos-relative{
                        padding-right: 0;
                    }
                }
                .carousel{
                    display: none;
                }
            }
        }
        .wrap-bottom{
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
            height: 40px;
            .address{
                line-height: 37px;
                font-size: 11px;
                padding-left: 10px;
                svg{
                    position: relative;
                    top: 2px;
                    margin-right: 5px;
                }
            }
            p.info{
                display: none;
            }
            .btn-project{
                height: 100%;
                border: none;
                padding: 10px;
                text-align: center;
                background: #135ea9;
                color: #fff;
                transition: .2s;
                font-size: 12px;
                text-transform: uppercase;
                letter-spacing: 1px;
                outline: none;
                cursor: pointer;
                display: block;
                &:hover{
                    background-color: ${props => props.theme.border};
                    color: #333;
                    path, circle{
                        fill: #333;
                    }
                }
                svg{
                    position: relative;
                    top: 2px;
                    margin-left: 5px;
                }
                
            }
            @media (max-width: 993px) {
                height: 60px;
                > .pos-relative{
                    align-items: center;
                }
                .btn-project{
                    display: none;
                }
                .address{
                    line-height: initial;
                }
                p.info{
                    display: block;
                    font-size: 11px;
                    padding-left: 10px;
                    svg{
                        position: relative;
                        top: 2px;
                        margin-right: 5px;
                    }
                }
            }
            @media (max-width: 452px){
                height: auto;
                padding: 10px;
                p{
                    margin-bottom: 10px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
        @media (max-width: 993px) {
            height: calc(100vh - 134px);
            margin-top: 74px;
            margin-bottom: 60px;
        }
    }
    .right-content{
        position: fixed;
        top: 58px;
        bottom: 40px;
        right: 0;
        width: 40px;
        > div{
            height: 100%;
        }
        .wrap-share{
            margin-bottom: 30px;
        }
        .wrap-button{
            margin-top: 20px;
            margin-bottom: 70px;
            transform: rotate(90deg);
            font-size: 12px;
            letter-spacing: 2px;
            cursor: pointer;
            color: ${props => props.theme.text};
            transition: .2s;
            font-weight: 500;
            &:hover{
                color: ${props => props.theme.primary};
            }
            .icon{
                position: relative;
                top: 2px;
                margin-right: 8px;
            }
        }
    }
}

`;