import React, { useRef, useState } from 'react';
import { NavLink } from 'react-router-dom';
import HomeStyles from './Home.style';
import {
    IconShare,
    IconYoutube,
    IconFacebook,
    IconTwitter,
    IconRight,
    IconAddress,
    IconSmartPhone,
    IconEmail,
    IconClock,
} from './../../utilities/SVG';
import { useGetData } from '../../utilities/api';
// import Slider from 'react-slick';

const Home = (props) => {

    const {
        theme,
        data
    } = props;

    const facebookShare = useRef(null);
    const twitterShare = useRef(null);
    const youtubeShare = useRef(null);

    const [toggleEvent, setToggleEvent] = useState(true);

    if(!data){
        return null;
    }

    const slideImages = data?.home?.slideImages || [];
    const eventImage = data?.home?.eventImage || "";

    // const settingsSlider = {
    //     infinite: true,
    //     speed: 500,
    //     slidesToShow: 1,
    //     slidesToScroll: 1,
    //     arrows: false,
    //     autoplay: true,
    //     autoplaySpeed: 4000,
    //     dots: true,
    //     dotsClass: 'custom-dots',
    //     customPaging: (i) => {
    //         return (
    //             <button>{i + 1}</button>
    //         )
    //     }
    // };

    const errorHandle = (e) => {
        // console.log(e.currentTarget);
        setToggleEvent(false);
    }

    return (
        <HomeStyles>
            <div className="top-header">
                <div className="d-flex">
                    <div className="wrap-content">
                        <p className="text-center">
                            <span className="icon"><IconSmartPhone color={'#fff'} width={15} height={15} /></span>
                            <span className="text">Phone: (08) 372 51993 – 136</span>
                        </p>
                    </div>
                    <div className="wrap-content">
                        <p className="text-center">
                            <span className="icon"><IconEmail color={'#fff'} width={15} height={15} /></span>
                            <span className="text">Email: ptn.httt@uit.edu.vn</span>
                        </p>
                    </div>
                    <div className="wrap-content">
                        <p className="text-center">
                            <span className="icon"><IconClock color={'#fff'} width={15} height={15} /></span>
                            <span className="text">T2 - T6: 8h - 17h</span>
                        </p>
                    </div>
                </div>
            </div>
            <div className="page page-1 main-container">
                <div className={"wrap-slider" + (toggleEvent ? " show-event" : "")}>
                    <div className="wrap-event d-flex justify-content-center">
                        <div className="pos-relative">
                            <div className="pos-relative d-flex align-items-center justify-content-center">
                                <img src={eventImage} alt="error" onError={errorHandle} />
                            </div>
                        </div>
                    </div>
                    <div className='carousel pos-relative' style={{ height: '100%' }}>
                        {/* <Slider {...settingsSlider}>
                            
                        </Slider> */}
                        <div className="wrap-carousel">
                            <div className="slide-images d-flex">
                            {slideImages.map((img, idx) => (
                                <div className={`img-container img-${idx + 1}`} key={`img-${idx + 1}`}>
                                    <img src={img} alt="" />
                                </div>
                            ))}
                            {/* clone: */}
                            {slideImages.map((img, idx) => (
                                <div className={`img-container img-${idx + 1}`} key={`img-clone-${idx + 1}`}>
                                    <img src={img} alt="" />
                                </div>
                            ))}
                            </div>
                        </div>
                    </div>


                    <div className="wrap-bottom">
                        <div className="pos-relative d-flex space-between" style={{ height: '100%' }}>
                            <div className="left-text">
                                <p className="address"><IconAddress color={theme.primary} width={15} height={15} />Trường ĐH Công nghệ Thông tin, Phòng E9.1</p>
                                <p className="info">
                                    <span className="icon"><IconSmartPhone color={theme.primary} width={15} height={15} /></span>
                                    <span className="text">Phone: (08) 372 51993 – 136</span>
                                </p>
                            </div>
                            <div className="right-text">
                                <NavLink to="/de-tai" className="btn-project">Nghiên cứu khoa học <IconRight color={'#fff'} width={15} height={15} /></NavLink>
                                <p className="info">
                                    <span className="icon"><IconEmail color={theme.primary} width={15} height={15} /></span>
                                    <span className="text">Email: ptn.httt@uit.edu.vn</span>
                                </p>
                                <p className="info">
                                    <span className="icon"><IconClock color={theme.primary} width={15} height={15} /></span>
                                    <span className="text">T2 - T6: 8h - 17h</span>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="right-content">
                    <div className="d-flex justify-content-center flex-column">
                        <div className="wrap-share text-center">
                            <IconShare color={theme.primary} width={20} height={20} />
                        </div>
                        <div className="pos-relative">
                            <a ref={youtubeShare} style={{ display: 'none' }} target='_blank' href="https://www.youtube.com/channel/UCS7laOuv00-9RDKsOHlI7eQ"></a>
                            <div className="wrap-button youtube d-flex align-items-center"
                                onClick={() => {
                                    youtubeShare.current.click();
                                }}
                            >
                                <span className="icon"><IconYoutube color={theme.primary} width={15} height={15} /></span>
                                <span className='text'>Youtube</span>
                            </div>
                        </div>
                        <div className="pos-relative">
                            <div className="fb-share-button"
                                data-href="http://islab.uit.edu.vn/"
                                data-layout="button_count"
                                data-size="small"
                                style={{ display: 'none' }}
                            >
                                <a ref={facebookShare} target="_blank" href="https://www.facebook.com/sharer/sharer.php?u=http%3A%2F%2Fislab.uit.edu.vn%2F&amp;src=sdkpreparse" className="fb-xfbml-parse-ignore">Chia sẻ</a>
                            </div>
                            <div className="wrap-button facebook d-flex align-items-center"
                                onClick={() => {
                                    facebookShare.current.click();
                                }}
                            >
                                <span className="icon"><IconFacebook color={theme.primary} width={15} height={15} /></span>
                                <span className='text'>Facebook</span>
                            </div>
                        </div>
                        <div className="pos-relative">
                            <a className="twitter-share-button"
                                target="_blank"
                                href="https://twitter.com/intent/tweet?text=Hello%20"
                                style={{ display: 'none' }}
                                ref={twitterShare}
                            >
                                Tweet</a>
                            <div className="wrap-button twitter d-flex align-items-center"
                                onClick={() => {
                                    twitterShare.current.click();
                                }}
                            >
                                <span className="icon"><IconTwitter color={theme.primary} width={15} height={15} /></span>
                                <span className='text'>Twitter</span>
                            </div>
                        </div>

                    </div>
                </div>
            </div>

            {/* <div className="page page-2 objective" ref={objectivePage}>

            </div> */}

        </HomeStyles>
    )
}

export default Home;